@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,900&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* rwd img css，可當作是 CSS Reset 其中一個 */
img {
  max-width: 100%;
  height: auto; }

/* 全域 border box */
*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

.container {
  width: 67.5%;
  margin: 0 auto; }
  @media (max-width: 992px) {
    .container {
      width: 720px; } }
  @media (max-width: 767px) {
    .container {
      width: 100%;
      padding-left: 13px;
      padding-right: 14px; } }

.bg-primary {
  background-color: #AA0601; }

.flex {
  display: flex; }

.wrap {
  flex-wrap: wrap; }

.justify-between {
  justify-content: space-between; }

.align-item-center {
  align-items: center; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px; }
  .header .logo {
    width: 80px;
    height: 72px;
    margin: 4px 0;
    background-image: url("https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/logo-white.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain; }
  .header ul {
    gap: 64px; }
    .header ul li {
      padding: 25px 0; }
    .header ul a {
      font-family: "Noto Sans TC", sans-serif;
      font-size: 20px;
      line-height: 1.5;
      color: white; }
    .header ul a:hover {
      color: black; }
  @media (max-width: 767px) {
    .header {
      flex-direction: column; }
      .header ul {
        gap: 0px;
        width: 100%;
        text-align: center; }
        .header ul li {
          width: 50%;
          padding: 12px 0;
          border: 1px solid white; }
        .header ul a {
          font-size: 16px; } }

.footer-contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 48px;
  border-bottom: 1px solid white; }
  .footer-contact .links {
    display: flex;
    gap: 48px; }
    .footer-contact .links a {
      font-family: "Noto Sans TC", sans-serif;
      font-size: 16px;
      line-height: 1.5;
      color: white; }
    .footer-contact .links a:hover {
      color: black; }
  .footer-contact .social-media {
    display: flex;
    gap: 32px; }
    .footer-contact .social-media img {
      width: 48px; }
  .footer-contact .contact-way {
    width: 100%;
    font-size: 36px;
    line-height: 1.5;
    font-family: "Noto Sans TC", sans-serif;
    margin: 24px 0;
    display: flex;
    flex-direction: column;
    gap: 8px; }
    .footer-contact .contact-way span {
      margin-right: 16px; }
    .footer-contact .contact-way a {
      color: white; }
    .footer-contact .contact-way a:hover {
      color: black; }
  @media (max-width: 767px) {
    .footer-contact {
      flex-direction: row-reverse;
      align-items: flex-start;
      padding-top: 24px; }
      .footer-contact .links {
        display: none; }
      .footer-contact .social-media {
        gap: 8px; }
        .footer-contact .social-media img {
          width: 24px; }
      .footer-contact .contact-way {
        width: initial;
        font-size: 16px;
        margin-top: 0; }
        .footer-contact .contact-way span {
          margin-right: 8px; } }

.footer-policy {
  display: flex;
  justify-content: space-between;
  color: white;
  padding: 32px 0; }
  .footer-policy .copyright {
    font-size: 16px;
    line-height: 1.5;
    font-family: "Roboto", sans-serif; }
  .footer-policy .tnc {
    display: flex;
    gap: 48px;
    font-size: 14px;
    line-height: 1.5; }
    .footer-policy .tnc a {
      color: white;
      font-family: "Noto Sans TC", sans-serif; }
    .footer-policy .tnc a:hover {
      color: black; }
  @media (max-width: 767px) {
    .footer-policy {
      flex-direction: column;
      gap: 8px;
      padding-top: 16px;
      padding-bottom: 24px; }
      .footer-policy .tnc {
        flex-direction: column;
        gap: 8px;
        font-size: 16px; } }

.box {
  width: 100px;
  height: 100px;
  background-color: red;
  display: flex;
  transition: all 0.5s; }
  .box:hover {
    width: 200px;
    background-color: blue; }
